<template>
  <div>
    <div class="pay-content" v-if="isPc">
      <div class="pay-method">
        <div class="method" v-for="(method, index) in payMethodList" :key="method.id" :class="{'method-selected': selectedMethod === index}">
          <div class="choose">
            <div class="pay-radio" :class="{'light': selectedMethod === index}">
              <i></i>
            </div>
            <div class="pay-detail">
              <div class="pay-logo">
                <i v-for="(icon, iconIndex) in method.logo" :key="iconIndex" :class="icon"></i>
              </div>
              <p class="method-name">{{$t(method.name)}}</p>
            </div>
          </div>
        </div>
      </div>
      <div>
        <div class="popover-arrow"></div>
        <div class="package-list">
          <div class="list-item">
            <p>{{$t('order/place:screen4.List1')}}</p>
            <p>{{$t(productInfo.name)}}</p>
          </div>
          <div class="list-item">
            <p>{{$t('order/place:screen4.List2')}}</p>
            <div class="sub-cycle-list-box">
              <div class="cycle-title-box" :class="{'show-cursor': selectBillingCycleList.length > 0}">
                <p>{{$t(productInfo.billingCycle === 'monthly' ?
                  'order/place:screen2:tab1.Tab' : productInfo.billingCycle === 'semiannually' ?
                  'order/place:screen2:tab3.Tab' : 'order/place:screen2:tab4.Tab')}}<span>{{productInfo['useTryPlan'] ? ' - 先試用7天' : ''}}</span></p>
                <i class="down-icon" v-if="!productInfo['useTryPlan'] && selectBillingCycleList.length > 0"></i>
              </div>
              <div class="cycle-list-box" v-if="!productInfo['useTryPlan'] && selectBillingCycleList.length > 0">
                <div class="popover-arrow" :class="{'popover-arrow-bg-change': false}"></div>
                <div class="cycle-content-box">
                  <p v-for="(item,index) in selectBillingCycleList" :key="index" @click="switchCycle(item)">
                    {{$t(item.name)}}</p>
                </div>
              </div>
            </div>
          </div>
          <div class="list-item">
            <p>{{$t('order/place:screen4.List3')}} </p>
            <p>{{productInfo['useTryPlan'] ? '0.9' : productInfo.payOriginalPrice}} USD</p>
          </div>
          <div class="list-item">
            <p>{{$t('order/place:screen4.List4-1')}}<span class="reduce-tips"
                                                          v-if="productInfo.billingCycle !== 'annually'">{{'（選擇年付更優惠）'}}</span>
            </p>
            <p class="reduce">- {{productInfo['useTryPlan'] ? 0 : productInfo.payReduce}} USD</p>
          </div>
          <div class="list-item" v-if="productInfo.isCanUseCoupon">
            <p>{{$t('order/place:screen4.List5')}}</p>
            <p class="reduce">- {{productInfo['useTryPlan'] ? 0 : productInfo.couponPrice}} USD</p>
          </div>
          <!--<div class="list-item">
            <p>{{'开启自动续费'}}</p>
            <a class="auto-renew-uncheck" :class="{'auto-renew-checked' : isAutoRenew}" @click="isAutoRenew = !isAutoRenew"></a>
          </div>-->
          <div class="pay-line"></div>
          <div class="total">
            <p>{{$t('order/place:screen4.List6')}}</p>
            <p>{{productInfo['useTryPlan'] ? '0.9' : productInfo.payPreferentialPrice}} USD</p>
          </div>
          <a-button class="submit-btn" type="primary"
                    :class="{'submit-btn-loading': iconLoading, 'disable-order': !canOrderInput}" icon="poweroff"
                    :loading="iconLoading"
                    @click="submitOrder">
            {{$t('order/place:screen4.Button')}}
          </a-button>
          <span class="error-tip" :class="{ 'error': !errorDisplay && !canLogin, 'login-tips': canLogin }">{{errorDescription}}<a
                  v-if="canLogin" @click="gotToLogin()"> 登入 </a><span v-if="canLogin">。</span></span>
          <div class="foot-tips">
            <ul>
              <li>{{$t('order/place:screen4.Tips-1-1')}}<span @click="goToTermsService">{{$t('order/place:screen4.Tips-1-2')}}</span>
              </li>
              <li>{{$t(cycleType !== 'onetime' ? 'order/place:screen4.Tips-2-1-1' : 'order/place:screen4.Tips-2-1-2')}}<span @click="goToPolicyDetail">{{$t('order/place:screen4.Tips-2-2')}}</span>
              </li>
              <li style="word-wrap:break-word;word-break:break-all;">{{$t('order/place:screen4.Tips-3')}}</li>
            </ul>

          </div>
        </div>
      </div>
      <div class="safe-tips">
        <div class="safe-logo">
          <i class="norton"></i>
          <i v-if="this.$i18n.locale !== 'en-US' " class="thirty-day-refund"></i>
          <i v-if="this.$i18n.locale === 'en-US'" class="thirty-day-refund-us"></i>

        </div>
        <p>{{$t('order/place:screen4.Features')}}</p>
        <div class="features">
          <p>{{$t('order/place:screen4.Paragraph-line-1')}}</p>
          <p>{{$t(cycleType !== 'onetime' ? 'order/place:screen4.Paragraph-line-2-1' :
            'order/place:screen4.Paragraph-line-2-2')}}</p>
          <p>{{$t('order/place:screen4.Paragraph-line-3')}}</p>
          <p>{{$t('order/place:screen4.Paragraph-line-4')}}</p>
          <p>{{$t('order/place:screen4.Paragraph-line-5')}}</p>
        </div>
      </div>
      <OrderPlaceDialog v-bind:articleId="articleId"></OrderPlaceDialog>
    </div>
    <div class="pay-content-mobile" v-if="!isPc">
      <div class="safe-logo">
        <i class="norton"></i>
        <i v-if="this.$i18n.locale !== 'en-US' " class="thirty-day-refund"></i>
        <i v-if="this.$i18n.locale === 'en-US'" class="thirty-day-refund-us"></i>
      </div>
      <div class="pay-method">
        <div class="method" v-for="(method,index) in isGoogle?payMethodListInApp:payMethodList" :key="index"
             :class="{'selected-item': selectedMethod === method.id, 'unselected': selectedMethod !== method.id}"
            @click="selectedPayment(index)"
        >
          <div class="choose">
            <p>{{$t(method.name)}}</p>
            <div class="pay-logo">
              <i v-for="(logo,index) in method.logo" :key="index" :class="logo"></i>
            </div>
            <div v-if="payMethodList.length > 0" class="radio"
                 :class="{'light': selectedMethod === method.id}">
            </div>
          </div>
          <div class="pay-info" v-if="selectedMethod === method.id">
            <div class="package-list">
              <div class="list-item">
                <p>{{$t('order/place:screen4.List1')}}</p>
                <p>{{$t(productInfo.name)}}</p>
              </div>
              <div class="list-item">
                <p>{{$t('order/place:screen4.List2')}}</p>
                <div class="sub-cycle-list-box">
                  <div class="cycle-title-box" :class="{'show-cursor': selectBillingCycleList.length > 0}">
                    <p>{{$t(productInfo.billingCycle === 'monthly' ?
                      'order/place:screen2:tab1.Tab' : productInfo.billingCycle === 'semiannually' ?
                      'order/place:screen2:tab3.Tab' : 'order/place:screen2:tab4.Tab')}}<span>{{productInfo['useTryPlan'] ? ' - 先試用7天' : ''}}</span></p>
                    <i class="down-icon" v-if="!productInfo['useTryPlan'] && selectBillingCycleList.length > 0"></i>
                  </div>
                  <div class="cycle-list-box" v-if="!productInfo['useTryPlan'] && selectBillingCycleList.length > 0">
                    <div class="popover-arrow" :class="{'popover-arrow-bg-change': false}"></div>
                    <div class="cycle-content-box">
                      <p v-for="(item,index) in selectBillingCycleList" :key="index" @click="switchCycle(item)">
                        {{$t(item.name)}}</p>
                    </div>
                  </div>
                </div>
              </div>
              <div class="list-item">
                <p v-if="!inAppTrial">{{$t('order/place:screen4.List3')}} </p>
                <p v-if="inAppTrial">{{$t('order/place:screen4.List3-1')}} </p>
                <p>{{productInfo['useTryPlan'] ? '0.9' : productInfo.payOriginalPrice}} USD</p>
              </div>
              <div class="list-item" v-if="!inAppTrial">
                <p>{{$t('order/place:screen4.List4-1')}}<span class="reduce-tips"
                                                              v-if="productInfo.billingCycle !== 'annually'">{{'（選擇年付更優惠）'}}</span>
                </p>
                <p class="reduce">- {{productInfo['useTryPlan'] ? 0 : productInfo.payReduce}} USD</p>
              </div>
              <div class="list-item" v-if="productInfo.isCanUseCoupon">
                <p>{{$t('order/place:screen4.List5')}}</p>
                <p class="reduce">- {{productInfo['useTryPlan'] ? 0 : productInfo.couponPrice}} USD</p>
              </div>
              <!--<div class="list-item">
                <p>{{'开启自动续费'}}</p>
                <a class="auto-renew-uncheck" :class="{'auto-renew-checked' : isAutoRenew}" @click="isAutoRenew = !isAutoRenew"></a>
              </div>-->
              <div class="total">
                <p>{{$t('order/place:screen4.List6')}}</p>
                <p>{{productInfo['useTryPlan'] ? '0.9' : productInfo.payPreferentialPrice}} USD</p>
              </div>
            </div>
            <div class="footer">
              <p>承諾30天可 <span @click="goToPolicyDetail()">退款</span>，請放心購買。 </p>
            </div>
          </div>
        </div>
      </div>
      <div class="submit" id="submit">
        <div class="protocol">
          <!--<div class="checkbox" @click="agreeProtocol = !agreeProtocol"
               :class="{'light': agreeProtocol}">
            <i></i>
          </div>-->
          <p>{{$t('order/place:screen4.Tips-1-1')}}</p>
          <span @click="goToTermsService">{{$t('order/place:screen4.Tips-1-2')}}</span>
        </div>
        <a-button class="submit-btn" type="primary"
                  :class="{'submit-btn-loading': iconLoading, 'disable-order': !canOrderInput}" icon="poweroff"
                  :loading="iconLoading"
                  @click="submitOrder">
          {{$t('order/place:screen4.Button')}}
        </a-button>
        <span id="bottomErrorTip" class="error-tip"
              :class="{ 'error': !errorDisplay && !canLogin, 'login-tips': canLogin  }">{{errorDescription}}<a
                v-if="canLogin" @click="gotToLogin()"> 登入 </a><span v-if="canLogin">。</span></span>
      </div>
      <div class="bottom-features">
        <p>{{$t('order/place:screen4.Features')}}</p>
        <div class="features">
          <p>{{$t('order/place:screen4.Paragraph-line-1')}}</p>
          <p>{{$t(cycleType !== 'onetime' ? 'order/place:screen4.Paragraph-line-2-1' :
            'order/place:screen4.Paragraph-line-2-2')}}</p>
          <p>{{$t('order/place:screen4.Paragraph-line-3')}}</p>
          <p>{{$t('order/place:screen4.Paragraph-line-4')}}</p>
          <p>{{$t('order/place:screen4.Paragraph-line-5')}}</p>
        </div>
      </div>
      <OrderPlaceDialog v-bind:articleId="articleId"></OrderPlaceDialog>
    </div>
  </div>
</template>

<script>
  import OrderPlaceDialog from '../dialog/OrderPlaceDialog';
  import {
    AU_HMAC,
    AU_UUID,
    AU_EMAIL,
    AU_AFF,
    AU_IN_APP,
    AD_PLATFORM, AU_PLATFORM
  } from "../requestServer";
  import {AU_IN_APP_TRIAL} from "../requestServer";
  import FingerprintJs from "@fingerprintjs/fingerprintjs";

  export default {
    name: 'PcPayment',
    components: {
      OrderPlaceDialog
    },
    data() {
      return {
        canLogin: false,
        isPc: this.$isPc(),
        isAutoRenew: false,
        emailInput: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '',
        canOrderInput: false,
        emailReg: /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/,
        isLogin: !!localStorage.getItem('token'),
        passwordBuf: String,
        errorDisplay: false,
        errorDescription: this.$t('order/place:screen4.Warning-1'),
        buttonDisabled: false,
        selectedMethod: localStorage.getItem("payMethodIndex") ? Number(localStorage.getItem("payMethodIndex")) : 0,
        cycleType: 'monthly',
        iconLoading: false,
        configoptions: {},
        payMethodList: [{
          id: 0,
          name: 'order/place:screen4.Tab1',
          logo: ['alipay'],
          logoUrl: [`../assets/pay/alipay.svg`],
          type: 'pkjeub-alipay'
        }],
        androidPayMethodList: [
          {
            id: 1,
            name: 'order/place:screen4.Tab1',
            logo: ['alipay'],
          },
        ],
        payMethodListInApp:[
          {
            id: 0,
            name: 'order/place:screen4.Tab1',
            logo: ['alipay'],
            logoUrl: [`../assets/pay/alipay.svg`],
            type: 'pkjeub-alipay'
          },{
            id: 1,
            name: 'order/place:screen4.Tab6',
            logo: ['google-pay'],
            logoUrl: [`../assets/pay/google-pay.svg`],
            type: 'google-pay'
          }
        ],
        productInfo: {},
        selectedBillCycleName: 'order/place:screen2:tab5.Tab',
        isBiennially: false,
        billingCycleList: [
          {
            name: 'order/place:screen2:tab1.Tab',
            cycleType: 'monthly'
          },
          {
            name: 'order/place:screen2:tab3.Tab',
            cycleType: 'semiannually'
          },
          {
            name: 'order/place:screen2:tab4.Tab',
            cycleType: 'annually'
          }
        ],
        selectBillingCycleList: [],
        articleId: '',
        agreeProtocol: false,
        inAppTrial: localStorage.getItem(AU_IN_APP_TRIAL) === 'true',
        inApp: localStorage.getItem(AU_IN_APP) === 'true',
        isGoogle: localStorage.getItem(AU_PLATFORM) === 'google',
        fingerprintResult: ''
      };
    },
    created() {

      // To avoid sending request before loading finger print result. Moving those logics to component level.

      (async () => {
        // Wait The FingerprintJS agent is ready.
        const agent = await FingerprintJs.load();
        const result = await agent.get();
        result.components.canvas = '{}'
        this.fingerprintResult = JSON.stringify(result);
      })();
    },
    props: {
      selectedProduct: {},
      email: {},
      canOrder: {}
    },
    watch: {
      selectedProduct: {
        immediate: true,
        handler(val) {
          if (val != undefined) {
            this.productInfo = val;
            this.updateCycle(this.productInfo.billingCycle);
          }
        }
      },
      email: {
        immediate: true,
        handler(val) {
          if (val != undefined) {
            this.emailInput = val;
            this.errorDisplay = false;
            this.canLogin = false;
          }
        }
      },
      canOrder: {
        immediate: true,
        handler(val) {
          if (val != undefined) {
            this.canOrderInput = val;
          }
        }
      }
    },
    methods: {
      gotToLogin() {
        // remove the client info
        localStorage.removeItem(AU_UUID);
        localStorage.removeItem(AU_HMAC);
        let routeUrl = this.$router.resolve({
          path: 'user/login',
          name: 'login',
          query: {...this.$route.query,  email: this.emailInput }
        });
        window.open(routeUrl.href, this.$inApp? '_self': '_blank');

      },
      updateCycle(selectedCycle) {
        if (selectedCycle === 'annually') {
          this.selectBillingCycleList = [];
        } else if (selectedCycle === 'semiannually') {
          this.selectBillingCycleList = this.billingCycleList.slice(2);
        } else {
          this.selectBillingCycleList = this.billingCycleList.slice(1);
        }
      },
      switchCycle(item) {
        this.$emit('billingCycleChange', item.cycleType);
      },
      submitOrder() {
        if (!this.canOrderInput) {
          this.errorDisplay = true;
          this.errorDescription = '请输入合法的邮箱';
          return;
        }
        this.errorDisplay = false;
        let param = {
          "recaptchaToken": "111111",
          "productId": this.productInfo.pid,
          "paymentMethod": this.inApp? this.payMethodListInApp[this.selectedMethod].type: this.payMethodList[this.selectedMethod].type,
          "billingcycle": this.productInfo.billingCycle,
          "isTrial": this.productInfo['useTryPlan']
        };
        if (this.emailInput && !this.emailReg.test(this.emailInput)) {
          this.errorDisplay = true;
          this.errorDescription = '請輸入合法的郵箱';
          return;
        } else {
          if (this.emailInput) param["email"] = this.emailInput;
        }
        if (this.productInfo.promocode) param["promocode"] = this.productInfo.promocode;
        if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
          param["uuid"] = localStorage.getItem(AU_UUID);
          param["hmac"] = localStorage.getItem(AU_HMAC);
        }
        // add aff
        if (localStorage.getItem(AU_AFF)) {
          param["aff"] = localStorage.getItem(AU_AFF)
        }
        this.iconLoading = true;
        this.canLogin = false;
        const that = this;

        // Send the events
        if (this.inApp) {
          // inApp event
          if (window.InappWebInterface) {
            // Call Android interface
            let params = {
              'method': this.emailInput? 'email': 'anonymity'
            };
            window.InappWebInterface.webEvent('sign_up', JSON.stringify(params));
          }
        } else {
          // gtag send gtag
          gtag('event', 'sign_up', {
            method: this.emailInput? 'email': 'anonymity'
          })
          // exo
          let exoclick = document.createElement("div");
          exoclick.setAttribute('style', 'display: none; visibility: hidden');
          exoclick.innerHTML = `<script  type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="339c888a0424d696979ae36c15041fc8"><` + `/script>`;
          document.body.appendChild(exoclick);
        }

        // get the device and traffic
        param['device'] = this.getDevice();
        param['traffics'] = this.getTraffic()

        this.$request.auSubmitOrder(param, function (data) {
          that.iconLoading = false;
          if (data && (data.status === 200 || data.status === 201) && data.data) {
            localStorage.setItem(AU_UUID, data.data.uuid);
            localStorage.setItem(AU_HMAC, data.data.hmac);
            if (that.emailInput && that.emailInput.length > 0 && !that.emailInput.endsWith('@auvpn.net')) {
              localStorage.setItem(AU_EMAIL, that.emailInput);
            }
            if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
              // let device = that.isPc? 'pc-web' : localStorage.getItem(AU_IN_APP)? 'android-web':'mobile-web'
              that.$request.auSendAdsToService( that.fingerprintResult,function (data) {
                if (data && (data.status === 200 || data.status === 201) && data.data) {
                  console.log('send ads =', data.data);
                }
              });
            }
            // send gtag
            let gtagData = {
              coupon: param.promocode ? param.promocode:'',
              currency: 'USD',
              value: that.productInfo['useTryPlan'] ? '0.9' : that.productInfo.payPreferentialPrice,
              traffic_type: process.env.NODE_ENV === 'production' ? 'normal': 'internal',
              items: [{
                item_id: param.productId,
                item_name: that.productInfo.name,
                promotion_id: 0,
                promotion_name: 'none',
                affiliation: localStorage.getItem(AU_AFF) ? localStorage.getItem(AU_AFF) : '',
                coupon: param.promocode ? param.promocode:'',
                item_variant: param.billingcycle,
                price: that.productInfo['useTryPlan'] ? '0.9' : that.productInfo.payPreferentialPrice,
                currency: 'USD'
              }]
            }

            // for google ads
            if (that.inApp) {
              // inApp event
              if (window.InappWebInterface) {
                // Call Android interface
                window.InappWebInterface.webEvent('place_order', JSON.stringify(gtagData));
              }
            } else {
              gtag('event', 'place_order', gtagData)
            }

            // for facebook ads
            fbq('track', 'InitiateCheckout', {
              content_category: param.billingcycle,
              content_ids: param.productId,
              currency: 'USD',
              num_items: 1,
              value: that.productInfo['useTryPlan'] ? 0.9 : Number(that.productInfo.payPreferentialPrice),
            });

            // for twitter ads
            twq('track','Signup');

            // for dable
            dablena('track', 'InitiateCheckout');

            if (that.inApp && that.selectedMethod === 1 ) {

              // if (that.productInfo.promocode) data["promocode"] = that.productInfo.promocode;
              if (window.InappWebInterface) {
                // pay by google-pay, call the android function to pay with google-pay
                let googlePayData = {
                  uuid: data.data.uuid,
                  hmac: data.data.hmac,
                  platform: 'google',
                  billingcycle: that.productInfo.billingCycle,
                  isTrial: that.productInfo['useTryPlan'],
                  productId: that.productInfo.pid
                }
                if (localStorage.getItem(AU_AFF)) {
                  googlePayData["aff"] = localStorage.getItem(AU_AFF)
                }
                window.InappWebInterface.webEvent('google_play', JSON.stringify(googlePayData));
              } else {
                that.errorDescription = 'Can not call the android web interface';
                that.errorDisplay = true;
              }

            } else {
              that.$router.push({
                path: 'order/pay',
                name: 'payment',
                params: {
                  redirect: data.data.redirectUrl,
                  gtagData: gtagData
                },
                query: {
                  invoiceId: data.data.invoiceId,
                }
              });
            }

            // let routeData = that.$router.resolve({
            //   name: 'payment',
            //   path: 'order/pay',
            //   params: {
            //     redirect: data.data.redirectUrl,
            //     gtagData: gtagData
            //   },
            //   query: {
            //     invoiceId: data.data.invoiceId
            //   }
            // });
            // window.open(routeData.href, '_blank');

            // event fire
            that.$eventFire.initiateCheckout()
          } else {
            if (data && data.message) {
              if (data.error === 'EmailRegistered') {
                that.errorDescription = '該電郵已註冊，可用账户地址'
                that.canLogin = true
              } else {
                that.errorDescription = data.message;
              }
              that.errorDisplay = true;
            } else {
              that.errorDescription = that.$t('order/place:screen4.Warning-2');
              that.errorDisplay = true;
            }
          }
        })
      },
      selectedPayment(index) {
        this.selectedMethod = index;
        localStorage.setItem("payMethodIndex",this.selectedMethod);
        // set the arrow positon

        // let offset = 110 * index + 46;
        // document.getElementsByClassName('popover-arrow')
        //   .item(0)
        //   .setAttribute('style', 'top:' + offset + 'px');
      },
      emailValidate(email) {
        const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        return emailReg.test(email);
      },
      goToTermsService() {
        this.articleId = Object.assign(360036448091);
        //window.open(`${window.location.origin}/help/article/360036448091`,'_blank');
      },
      goToPolicyDetail() {
        this.articleId = Object.assign(360004852651);
        //window.open(`${window.location.origin}/help/article/360004852651`,'_blank');
      },
      getDevice() {
        let uA = navigator.userAgent.toLowerCase();
        let ipad = uA.match(/ipad/i) !== null;
        let iphone = uA.match(/iphone/i) !== null;
        let android = uA.match(/android/i) !== null;
        let windows = uA.match(/windows/i) !== null;
        let mac = uA.match(/mac os/i) !== null;
        let inApp = localStorage.getItem(AU_IN_APP)
        if (inApp) {
          return 'android-app'
        } else {
          return ipad || iphone ? 'ios-browser': android ? 'android-browser': windows ? 'pc-browser' : mac ? 'mac-browser' : 'unknown'
        }
      },
      getTraffic() {
        let traffics = []
        if (localStorage.getItem(AD_PLATFORM)) {
          traffics.push(localStorage.getItem(AD_PLATFORM))
        }
        // aff traffic
        if (localStorage.getItem(AU_AFF)) {
          traffics.push('aff')
        }
        // nature traffic
        if (traffics.length === 0) {
          traffics.push('nature')
        }

        return traffics
      }
    }
  };
</script>

