<script>
    /* eslint-disable */
    import {AU_EMAIL, AU_EMAIL_STATUS, AU_HMAC, AU_SELECTED_SERVICE, AU_UUID} from "@/requestServer";

    export default {
        name: 'PcProducts',
        components: {},
        data() {
            return {
                selectedProductIndex: 0,
                packageList: [],
                cardInfo: {},
                hideTryPlan: false,
                isPc: this.$isPc(),
                packageIndex: 1,
                triggerMethod: 'click',
                visible: [false, false, false, false, false],
                timeout: -1,
                canTrial: true
            }
        },
        props: {
            productList: {},
            selectedProduct: {},
            triable: {}
        },
        watch: {
            productList: {
                immediate: true,
                handler(value) {
                    if (value !== undefined) {
                        this.packageList = value;
                    }
                }
            },
            selectedProduct: {
                immediate: true,    // 这句重要
                handler(val) {
                    if (val != undefined) {
                        this.selectedProductIndex = val.billingCycle === 'annually' ? 0 : val.billingCycle === 'semiannually' ? 1 : 2;
                    }
                }
            },
            triable: {
                immediate: true,    // 这句重要
                handler(val) {
                    if (val != undefined) {
                        this.canTrial = val
                    }
                }
            }
        },
        created() {
            this.cardInfo = this.$t('order/place:screen2:tab1');
            if (localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
                if (localStorage.getItem(AU_SELECTED_SERVICE)) {
                    this.hideTryPlan = true;
                } else {
                    const that = this;
                    this.$request.auGetAllServices('', '', function (data) {
                        if (data && (data.status === 200 || data.status === 201) && data.data) {
                            if ((data.data.jjService && data.data.jjService.length > 0) || (data.data.unpaidService && data.data.unpaidService.length > 0)) {
                                that.hideTryPlan = true;
                            }
                        }
                    });
                }
            }
        },
        methods: {
            showTips(index) {
                this.visible = [false, false, false, false, false]
                this.$set(this.visible, index, true)
                const that = this
                if (this.timeout !== -1) {
                    clearTimeout(this.timeout)
                }
                this.timeout = setTimeout(function () {
                    that.$set(that.visible, index, false)
                }, 3000)
            },
            clickTipsBox(item, index) {
                item.useTryPlan = !item.useTryPlan;
                this.stepOneClick(index);
            },
            stepOneClick(e) {
                this.selectedProductIndex = e;
                this.packageList.forEach((item, index) => {
                    if (index !== e) {
                        item.useTryPlan = false;
                    }
                });
                this.$emit('selectedProduct', this.packageList[e]);

                // event fire
                this.$eventFire.customizeProduct()
            },
            switchPackage(index) {
                this.packageIndex = index
                if (index === 1) {
                    this.packageList[0].useTryPlan = false
                    this.stepOneClick(0)
                } else {
                    this.clickTipsBox(this.packageList[0], 0)
                }

                // event fire
                this.$eventFire.customizeProduct()
            }
        }
    };
</script>

