<template>
    <div class="pay-order-wrap">
        <div class="loading" v-if="false">
            <Loading></Loading>
        </div>
        <div :class="{'pay-order-pc': isPc,'pay-order-mobile': !isPc}">
            <PcIntroduce v-if="!inAppTrial"></PcIntroduce>
            <section v-if="!inAppTrial" class="step step1">
                <div class="header">
                    <div class="header-left">
                        <span class="header-index">1</span>
                        {{$t('Product.Select-Package')}}
                    </div>
                    <div class="coupon-box" :class="{'add-coupon':isAddCoupon && !isUseCoupon,'use-coupon':isAddCoupon && couponCode.length > 0 && !isUseCoupon,
          'use-coupon-success':useCouponSuccess && isUseCoupon,'use-coupon-failure':!useCouponSuccess && isUseCoupon}"
                         v-if="isPc">
                        <p class="p-add" v-if="!isAddCoupon || isUseCoupon">{{!isAddCoupon ?
                            $t('Product.Use-Coupon') :
                            isUseCoupon ? useCouponSuccess ? $t('Product.Used-Coupon') : $t('Product.Invalid-Coupon') : ''}}</p>
                        <a-input :placeholder="$t('Product.Please-Enter-Coupon')" v-model="couponCode"
                                 v-if="isAddCoupon && !isUseCoupon"></a-input>
                        <a class="i-use-coupon" v-if="isAddCoupon && couponCode.length > 0 && !isUseCoupon"
                           @click="useCoupon"></a>
                        <a class="i-add-coupon" v-if="isAddCoupon || !isAddCoupon || isUseCoupon"
                           @click="addCoupon()"></a>
                    </div>
                </div>
                <PcProducts
                        @selectedProduct="updateProduct"
                        v-bind:triable="canTrial"
                        v-bind:selectedProduct="selectedProduct"
                        v-bind:productList="productsList"></PcProducts>
                <div class="coupon-container">
                    <div class="coupon-box" :class="{'add-coupon':isAddCoupon && !isUseCoupon,'use-coupon':isAddCoupon && couponCode.length > 0 && !isUseCoupon,
          'use-coupon-success':useCouponSuccess && isUseCoupon,'use-coupon-failure':!useCouponSuccess && isUseCoupon}"
                         v-if="!isPc">
                        <p class="p-add" v-if="!isAddCoupon || isUseCoupon" @click="addCoupon()">{{!isAddCoupon ?
                            $t('Product.Use-Coupon2') :
                            isUseCoupon ? useCouponSuccess ? $t('Product.Used-Coupon2') : $t('Product.Invalid-Coupon2') : ''}}</p>
                        <a-input :placeholder="$t('Product.Please-Enter-Coupon2')" v-model="couponCode"
                                 v-if="isAddCoupon && !isUseCoupon"></a-input>
                        <a class="i-use-coupon" v-if="isAddCoupon && couponCode.length > 0 && !isUseCoupon"
                           @click="useCoupon"></a>
                        <a class="i-add-coupon" v-if="isAddCoupon || !isAddCoupon || isUseCoupon"
                           @click="addCoupon()"></a>
                    </div>
                </div>
            </section>
            <section v-if="inAppTrial" class="step inapp-step1">
                <div class="step-container">
                    <div class="text-container">
                        <span class="title">{{$t('Product.Seven-Day-Try') }}</span>
                        <span class="content">{{$t('Product.Try-First') }}</span>
                        <span class="content">{{$t('Product.No-Bill-Create') }}</span>
                    </div>
                    <div class="price-container">
                        <span class="dollar-symbol">$</span>
                        <span class="price"> 0.9</span>
                    </div>
                </div>
            </section>
            <div class="email-section" :class="{'email_section-inapp': inAppTrial}">
                <section class="step2">
                    <div class="header">
                        <span v-if="!inAppTrial" class="header-index">2</span>
                        <span v-if="inAppTrial" class="header-index">1</span>
                      {{$t('Product.Create-Account') }}
                    </div>
                    <div class="email-section-content">
                        <CreateAccount
                                v-on:client-Info="clientInfo"
                                v-bind:userEmail="inAppEmail"
                                v-bind:anonymousAble="!selectedProduct.useTryPlan"
                        ></CreateAccount>
                        <!--            <div class="content-right-box">-->
                        <!--              <span class="content-title">绑定电邮（选填）</span>-->
                        <!--              <a-input placeholder="電郵地址" v-model="email" @change="emailChange" :disabled="disabledEmail"></a-input>-->
                        <!--              <span style="color: #A5B1BF;" class="bottom-tips">绑定电邮方便您更快找回账户。<br>我们承诺不会泄露您的电邮信息。</span>-->
                        <!--            </div>-->
                    </div>
                </section>
            </div>
            <section v-if="inAppTrial" class="step inapp-coupon">
                <div class="header">
                    <span class="header-index">2</span>
                  {{$t('Product.Enter-Coupon') }}
                </div>
                <a-input class="coupon-input" :placeholder="$t('Product.Please-Enter-Coupon3')"
                         v-model="couponCode"></a-input>
            </section>
            <section class="step step3">
                <div class="header">
                    <span class="header-index">3</span>
                    {{$t('Product.Pay-Method')}}
                </div>
                <PcPayment
                        @billingCycleChange="updateCycle"
                        v-bind:email="email"
                        v-bind:canOrder="canOrder"
                        v-bind:selectedProduct="selectedProduct"></PcPayment>
            </section>
        </div>
    </div>
</template>

<script>
    import Loading from '../components/Loading';
    import PcIntroduce from '../components/PcIntroduce';
    import PcProducts from '../components/PcProducts';
    import PcPayment from '../components/PcPayment';
    import CreateAccount from "../components/CreateAccount";
    import {AU_EMAIL, AU_EMAIL_STATUS, AU_HMAC, AU_IN_APP_TRIAL, AU_UUID, AU_IN_APP} from "../requestServer";

    export default {
        name: 'pay-order',
        components: {
            Loading,
            PcIntroduce,
            PcProducts,
            CreateAccount,
            PcPayment,
        },
        data() {
            return {
                isPc: this.$isPc(),
                isAddCoupon: false,
                couponCode: '',
                isUseCoupon: false,
                useCouponSuccess: false,
                couponInfo: {},
                userAddress: '',
                productsList: [],
                selectedProduct: {},
                canOrder: false,
                email: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? '' : localStorage.getItem(AU_EMAIL) : '',
                disabledEmail: localStorage.getItem(AU_EMAIL) ? localStorage.getItem(AU_EMAIL).endsWith('@auvpn.net') && localStorage.getItem(AU_EMAIL).length > 40 ? false : true : false,
                inAppTrial: false,//localStorage.getItem(AU_IN_APP_TRIAL) === 'true',
                inApp: localStorage.getItem(AU_IN_APP) === 'true',
                inAppEmail: '',
                canTrial: false,
                eventInterval: null,
                eventIntervalCount: 0
            };
        },
        created() {
            this.isLogin = localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC);
            if (this.isLogin) {
                const that = this
                this.$request.auGetAllServices('','',function (data) {
                    if (data && (data.status === 200 || data.status === 201) && data.data) {
                        localStorage.setItem(AU_EMAIL, data.data.email);
                        localStorage.setItem(AU_EMAIL_STATUS, data.data.status);
                        that.inAppEmail = data.data.email
                        that.email = data.data.email

                        that.canTrial = data.data['jjService'].length <= 0 && data.data['unpaidService'].length <= 0
                    }
                });
            } else {
                this.canTrial = true
            }
        },

        mounted() {
            this.getProducts();
            let exoclick = document.createElement("div");
            exoclick.setAttribute('style', 'display: none; visibility: hidden');
            exoclick.innerHTML = `<script type="application/javascript" src="https://a.exoclick.com/tag_gen.js" data-goal="339c888a0424d696979ae36c15041fc8"><` + `/script>`;
            document.body.appendChild(exoclick);

            // event fire
            this.$eventFire.viewOrderPage()
            const that = this
            this.eventInterval = setInterval(function () {
                if (that.eventIntervalCount > 120) {
                    window.clearInterval(that.eventInterval)
                } else {
                    switch (that.eventIntervalCount) {
                        case 30:
                            that.$eventFire.orderPageEngage30s()
                            break;
                        case 60:
                            that.$eventFire.orderPageEngage60s()
                            break;
                        case 120:
                            that.$eventFire.orderPageEngage120s()
                            break
                    }
                    that.eventIntervalCount ++
                }
            }, 1000)
        },
        methods: {
            clientInfo(canOrder, email) {
                this.canOrder = canOrder
                this.email = email
            },
            updateProduct(selectedP) {
                this.selectedProduct = Object.assign({}, selectedP, {});
            },
            updateCycle(cycle) {
                this.selectedProduct = Object.assign({}, this.productsList[cycle === 'annually' ? 0 : cycle === 'semiannually' ? 1 : 2], {});
            },
            getProducts() {
                let that = this;
                let gid = process.env.NODE_ENV === 'production' ? 1 : 3;
                this.$request.auGetProducts(gid, function (data) {
                    if (data && data.status === 200) {
                        let array = [];
                        let p = data.data.products[0];
                        p['useTryPlan'] = that.inAppTrial;
                        array.push(p);
                        array.push(p);
                        array.push(p);
                        that.productsList = JSON.parse(JSON.stringify(array));
                        that.productsList[0]['billingCycle'] = "annually";
                        that.productsList[1]['billingCycle'] = "semiannually";
                        that.productsList[2]['billingCycle'] = "monthly";
                        that.calculatePrice();
                        that.selectedProduct = Object.assign({}, that.inAppTrial? that.productsList[2]: that.productsList[0], {});
                    }
                });
            },
            calculatePrice() {
                this.configoptionsBuf = [];
                this.productsList.forEach(item => {
                    // get the device limit price
                    let devicePrice = 0;
                    let devicePriceMonthly = 0;
                    let bandwidth = 0;
                    let bandwidthMonthly = 0;
                    item['configoptions']['configoption'].map(config => {
                        if (config.name.indexOf('Device Limit') > -1) {
                            devicePrice = Number(config['options']['option'][0]['pricing']['USD'][item.billingCycle]) * this.deviceNumber;
                            devicePriceMonthly = Number(config['options']['option'][0]['pricing']['USD']['monthly']) * this.deviceNumber;
                        }
                        if (config.name.indexOf('Addon Bandwidth') > -1) {
                            bandwidth = Number(config['options']['option'][0]['pricing']['USD'][item.billingCycle]) * this.bandwidthNumber;
                            bandwidthMonthly = Number(config['options']['option'][0]['pricing']['USD']['monthly']) * this.bandwidthNumber;
                        }
                    });
                    // calculate the price
                    switch (item.billingCycle) {
                        case 'monthly':
                            item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                            item.preferentialPrice = this.mathRound(item.originalPrice);
                            item.payReduce = 0;
                            item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly) + devicePriceMonthly + bandwidthMonthly);
                            item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.monthly) + devicePrice + bandwidth);
                            item.monthly = item.payPreferentialPrice;
                            break;
                        case 'semiannually':
                            item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                            item.preferentialPrice = this.mathRound(Number(item.pricing.USD.semiannually * 2) + devicePrice * 2 + bandwidth * 2);
                            item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 6) + devicePriceMonthly * 6 + bandwidthMonthly * 6);
                            item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.semiannually) + devicePrice + bandwidth);
                            item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
                            item.monthly = this.mathRound(item.payPreferentialPrice / 6);
                            break;
                        case 'annually':
                            item.originalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                            item.preferentialPrice = this.mathRound(Number(item.pricing.USD.annually) + devicePrice + bandwidth);
                            item.payOriginalPrice = this.mathRound(Number(item.pricing.USD.monthly * 12) + devicePriceMonthly * 12 + bandwidthMonthly * 12);
                            item.payPreferentialPrice = this.mathRound(Number(item.pricing.USD.annually) + devicePrice + bandwidth);
                            item.payReduce = this.mathRound(item.payOriginalPrice - item.payPreferentialPrice);
                            item.monthly = this.mathRound(item.payPreferentialPrice / 12);
                            break;
                    }
                    item.savePrice = this.mathRound((item.payOriginalPrice - item.payPreferentialPrice) / item.payOriginalPrice * 100);
                });
                this.compareSavePercent();
            },
            compareSavePercent() {
                if (this.productsList && this.productsList.length > 0) {
                    let saveArray = [];
                    this.productsList.forEach(item => {
                        saveArray.push(item.savePrice);
                    });
                    saveArray = saveArray.sort(function (a, b) {
                        return b - a
                    });
                    this.productsList.forEach(item => {
                        if (item.savePrice === saveArray[0]) {
                            item.savePercent = 0;
                        } else if (item.savePrice === saveArray[2]) {
                            item.savePercent = 2;
                        } else {
                            item.savePercent = 1;
                        }
                    });
                }
                this.updateCycle(this.selectedProduct.billingCycle);
                this.productsList = this.productsList.filter(item => {
                    return item.pid;
                });
            },
            mathRound(num) {
                return Math.round(num * 100) / 100;
            },
            addCoupon() {
                this.isAddCoupon = !this.isAddCoupon;
                if (!this.isAddCoupon) {
                    this.isUseCoupon = false;
                    this.couponCode = '';
                    this.productsList.forEach(item => {
                        if (item.isCanUseCoupon) {
                            item.isCanUseCoupon = false;
                            item.couponPrice = 0;
                            item.payPreferentialPrice = this.mathRound(item.payOriginalPrice - item.payReduce);
                            item.savePrice = this.mathRound((item.payOriginalPrice - item.payPreferentialPrice) / item.payOriginalPrice * 100);
                        }
                    });
                    this.compareSavePercent();
                }
            },
            useCoupon() {
                const that = this;
                this.$request.auGetPrmotions(this.couponCode, function (data) {
                    that.isUseCoupon = true;
                    if (data && data.status === 200) {
                        if (data.data && data.data.products.promotions) {
                            let promo = data.data.products.promotions.promotion[0];
                            if (that.checkCouponTime(promo.startdate, promo.expirationdate) && that.checkProductIdCycle(promo.appliesto, promo.cycles)) {
                                that.useCouponSuccess = true;
                                that.productsList.forEach(item => {
                                    if (item.isCanUseCoupon) {
                                        item.couponMonthly = that.mathRound(item.monthly * (100 - Number(promo.value)) / 100);
                                        item.couponPrice = that.mathRound(item.payPreferentialPrice * Number(promo.value) / 100);
                                        item.payPreferentialPrice = that.mathRound(item.payPreferentialPrice - item.couponPrice);
                                        item.promocode = that.couponCode;
                                    } else {
                                        item.couponPrice = 0;
                                        item.promocode = '';
                                    }
                                    item.savePrice = that.mathRound((item.payOriginalPrice - item.payPreferentialPrice) / item.payOriginalPrice * 100);
                                });
                                that.compareSavePercent();
                            } else {
                                that.useCouponSuccess = false;
                            }
                        } else {
                            that.useCouponSuccess = false;
                        }
                    } else {
                        that.useCouponSuccess = false;
                    }
                });
            },
            checkProductIdCycle(ids, cycles) {
                this.productsList.forEach(item => {
                    let isCanUseCoupon = false;
                    if (ids && cycles) {
                        ids.split(",").forEach(id => {
                            if (item.pid.toString() === id) {
                                cycles.split(",").forEach(cycle => {
                                    if (cycle.toLowerCase() === item.billingCycle
                                        || (cycle === 'Semi-Annually' && item.billingCycle === 'semiannually')) {
                                        isCanUseCoupon = true;
                                    }
                                });
                            }
                        });
                    } else if (ids.length === 0 && cycles) {
                        cycles.split(",").forEach(citem => {
                            if (citem.toLowerCase() === item.billingCycle
                                || (citem === 'Semi-Annually' && item.billingCycle === 'semiannually')) {
                                isCanUseCoupon = true;
                            }
                        });
                    } else if (ids && cycles.length === 0) {
                        ids.split(",").forEach(pid => {
                            if (item.pid.toString() === pid) {
                                isCanUseCoupon = true;
                            }
                        });
                    } else {
                        isCanUseCoupon = true;
                    }
                    item.isCanUseCoupon = isCanUseCoupon
                });
                let canUseCouponArray = this.productsList.filter(item => {
                    return item.isCanUseCoupon;
                });
                return canUseCouponArray.length > 0;
            },
            checkCouponTime(startTime, endTime) {
                let isInValidityPerid = false;
                if (startTime === '0000-00-00' || this.countCouponTime(startTime, new Date()) >= 0) {
                    if (endTime === '0000-00-00' || this.countCouponTime(new Date(), endTime) > 0) {
                        isInValidityPerid = true;
                    }
                }
                return isInValidityPerid;
            },
            countCouponTime(sDate1, sDate2) {
                let time1 = Date.parse(sDate1);
                let time2 = Date.parse(sDate2);
                let nDays = Math.abs(parseInt((time2 - time1) / 1000));
                return nDays;
            },
        }
    };
</script>

