<template>
  <div :class="{'create-account-box': isPc,'create-account-box-mobile': !isPc, 'canAnonymous': !canAnonymous&&isPc, 'canAnonymous-mobile': !canAnonymous&&!isPc}">
    <div class="email-client" :class="{'chosen-email-client': isEmailClient, 'email-client-bg': !isEmailClient}" :style="{'z-index': isEmailClient? 1 :0}"
         @click="clientModeSwitch(true)">
      <div class="client-header">
        <span class="header-title" :class="{'header-selected': isEmailClient}">{{$t('CreateAccount.Email-Account')}}</span>
        <span class="header-tip">{{$t('CreateAccount.Easy-Use')}}</span>
      </div>
      <div class="client-info" v-show="isEmailClient">
        <a-input class="email-input" :placeholder="$t('CreateAccount.Enter-Email')" v-model="clientEmail" @focusout="emailFocusout()"
                 @focusin="emailFocusin()" :disabled="isLogin && isBoundEmail"
                 @change="emailChange()"
                 @pressEnter="emailFocusout()"/>
        <div class="tips-container">
          <span class="tips-msg" v-if="showTips" :class="{'error-msg': isErrorMsg}">{{tipsMsg}}
            <a v-if="canLogin" @click="gotToLogin()" style="margin: 0px 4px;"> {{$t('CreateAccount.Login')}} </a>。
          </span>
          <ul class="all-tips">
            <li class="input_tips">{{$t('CreateAccount.Tired-Email')}}</li>
            <li class="input_tips">{{$t('CreateAccount.No-Leak')}}</li>
          </ul>
        </div>

      </div>
    </div>
    <div v-if="canAnonymous" class="anonymous-client" :class="{'chosen-anonymous-client': !isEmailClient && canSwitch, 'anonymous-client-bg': isEmailClient}"
         :style="{'z-index': isEmailClient? 0:1, 'cursor': canSwitch?'pointer': 'not-allowed'}" @click="clientModeSwitch(false)">
      <div class="client-header">
        <span class="header-title" :class="{'header-selected': !isEmailClient}">{{$t('CreateAccount.Anonymous-Account')}}</span>
        <span class="header-tip">{{$t('CreateAccount.Privacy')}}</span>
      </div>
      <div class="client-info" v-show="!isEmailClient">
        <div class="btn-container" v-if="userAddress.length <= 0 ">
          <a-button class="use-btn" type="primary" @click="createAccount()" :loading="creating">{{$t('CreateAccount.Create-Anonymous')}}</a-button>
        </div>
        <div class="addr-container" v-if="userAddress.length > 0 ">
          <span class="user-address" :disabled="isLogin">{{userAddress}}</span>
          <a-tooltip placement="top" trigger="click" :mouseLeaveDelay=1 :destroyTooltipOnHide=true
                     :visible="!hideCopyTips">
            <template slot="title">
              <span>{{$t('CreateAccount.Copied')}}</span>
            </template>
            <span class="copy-tips" @click="copyAddr()" @mouseleave="hideCopyTips = true">{{$t('CreateAccount.Copy-Account-Address')}}</span>
          </a-tooltip>
          <div class="addr-footer">
            <span class="download-span" :class="{'download-span-mobile': !isPc}" @click="downloadAuAccountInfo()">{{isPc? $t('CreateAccount.Download-Account-Info'): $t('CreateAccount.Copy-Account-Info') }}</span>
            <a v-if="isPc" class="drag-bg" :href="userAddress" style="position: absolute;opacity: 0" @click.prevent="preventOpenAUAdress">{{$t('CreateAccount.Au-Account')}}</a>
            <span class="favorite-span">{{isPc? $t('CreateAccount.Draw-To-Bookmark'): $t('CreateAccount.Add-Mobile-Desktop')}}</span>
          </div>
        </div>
        <ul class="all-tips">
          <li v-if="userAddress.length <= 0" class="input_tips1">{{$t('CreateAccount.Single-Way')}}</li>
          <li v-if="userAddress.length <= 0" class="input_tips1">{{$t('CreateAccount.Forgotten')}}</li>
          <li v-if="userAddress.length > 0" class="input_tips">{{$t('CreateAccount.Save-Properly')}}</li>
        </ul>
      </div>
    </div>
  </div>
</template>

<script>
  import {AU_HMAC, AU_SELECTED_SERVICE, AU_UUID, AU_EMAIL, AU_EMAIL_STATUS} from "../requestServer";

  export default {
    name: 'CreateAccount',
    data() {
      return {
        intervalStartTime: 0,
        creating: false,
        isErrorMsg: false,
        showTips: false,
        tipsMsg: this.$t('order/place:screen3.error-msg-1'),
        canLogin: false,
        clientEmail: '',
        isBoundEmail: false,
        emailChecking: false,
        isPc: this.$isPc(),
        isEmailClient: true,
        userAddress: '',
        isLogin: false,
        showCopy: false,
        canOrder: false,
        hideCopyTips: true,
        accountInterval: {},
        canSwitch: true,
        canAnonymous: true,
        validate: false
        // showBottomTips: !this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC) && localStorage.getItem(AU_SELECTED_SERVICE)
      };
    },
    props: {
      userAccount: {},
      userEmail: {},
      anonymousAble: {}
    },
    watch: {
      userAccount: {
        immediate: true,
        handler(value) {
          if (value !== undefined) {
            this.userAddress = value.address;
            //this.showBottomTips = !this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC) && localStorage.getItem(AU_SELECTED_SERVICE);
          }
        }
      },
      userEmail: {
        immediate: true,
        handler(value) {
          if (value !== undefined && !value.endsWith('@auvpn.net')) {
            this.clientEmail = value
            this.updateStatus()
          }
        }
      },
      anonymousAble: {
        immediate: true,
        handler(value) {
          if (value !== undefined) {
            this.canAnonymous = value
            this.clientModeSwitch(true)
          }
        }
      }
    },
    mounted() {
      if (!this.$route.path.endsWith('order/pay') && localStorage.getItem(AU_UUID) && localStorage.getItem(AU_HMAC)) {
        this.isLogin = true
        this.userAddress = `https://www.ausvc.link/?uuid=${localStorage.getItem(AU_UUID)}&hmac=${localStorage.getItem(AU_HMAC)}`;
        let email = localStorage.getItem(AU_EMAIL)
        if (email) {
          this.isBoundEmail = email.length > 0 && !email.endsWith('@auvpn.net')
        }
        if (this.isBoundEmail) {
          this.clientEmail = email
          this.showTips = true
          this.tipsMsg = this.$t('order/place:screen3.paragrahp-line-3')
          this.canLogin = false
          this.canOrder = true
          this.canSwitch = false
          this.$emit('client-Info', this.isBoundEmail, email);
        } else {
          // use the anonymous mode
          this.clientModeSwitch(false)
        }
      }
    },
    methods: {
      updateStatus() {
        if (this.clientEmail) {
          this.isBoundEmail = this.clientEmail.length > 0 && !this.clientEmail.endsWith('@auvpn.net')
          if (this.isBoundEmail) {
            this.isLogin = true
            this.isEmailClient = true
            this.canOrder = true
            this.canSwitch = false
          }
        }
      },
      gotToLogin() {
        // remove the client info
        localStorage.removeItem(AU_UUID);
        localStorage.removeItem(AU_HMAC);
        let routeUrl = this.$router.resolve({
          path: 'user/login',
          name: 'login',
          query: {...this.$route.query,  email: this.clientEmail }
        });
        window.open(routeUrl.href, this.$inApp? '_self': '_blank');
      },
      emailChange() {
        if (this.clientEmail.length <= 0) {
          return
        }
        this.validate = this.emailValidate(this.clientEmail)
        this.$emit('client-Info', this.validate, this.clientEmail);
      },
      copyAddr() {
        let textarea = document.createElement("textarea");
        textarea.value = this.userAddress;
        textarea.readOnly = true;
        document.body.appendChild(textarea);
        textarea.select(); // 选择对象
        document.execCommand("Copy"); // 执行浏览器复制命令
        textarea.className = "textarea";
        textarea.style.display = "none";
        const that = this
        setTimeout(function () {
          // Open the channel
          that.hideCopyTips = true
        }, 1500);
        this.hideCopyTips = false

      },
      clientModeSwitch(isEmail) {
        if (!isEmail && this.canSwitch) {
          this.isEmailClient = false
          this.canOrder = true
          this.$emit('client-Info', this.canOrder, '');
        } else {
          this.isEmailClient = true
          // can order if there are new email or the email already bound
          this.canOrder = this.emailValidate(this.clientEmail) || this.isBoundEmail
          this.$emit('client-Info', this.canOrder, this.clientEmail);
        }
      },
      emailFocusout() {
        if (!this.isEmailClient) {
          this.$emit('client-Info', this.canOrder, '');
          return;
        }
        if (this.clientEmail.length <= 0) {
          return;
        }
        if (!this.emailValidate(this.clientEmail)) {
          this.showTips = true;
          this.isErrorMsg = true;
          this.canOrder = false;
          this.tipsMsg = this.$t('order/place:screen3.error-msg-1');
          return
        }
        this.emailChecking = true;
        const that = this;
        this.$request.auCheckEmail({email: this.clientEmail}, function (data) {
          if (!that.isEmailClient) {
            that.$emit('client-Info', that.canOrder, '');
            return;
          }
          that.emailChecking = false;
          if (data.status === 200 || data.status === 201) {
            if (data.data.result === 'success') {
              that.showTips = true
              that.tipsMsg = that.$t('order/place:screen3.paragrahp-line-3')
              that.canLogin = true;
              that.canOrder = false;
              that.$emit('client-Info', that.canOrder, that.clientEmail);
              localStorage.removeItem(AU_UUID);
              localStorage.removeItem(AU_HMAC);
              that.intervalStartTime = new Date().getTime()
              that.accountInterval = setInterval(function () {
                // 0 - 3 min query status per 5s
                if (new Date().getTime() - that.intervalStartTime > 1000 * 60 * 3) {
                  window.clearInterval(that.accountInterval);
                  that.accountInterval = setInterval(function () {
                    // 3 - 10 min query status per 8s
                    if (new Date().getTime() - that.intervalStartTime > 1000 * 60 * 10) {
                      window.clearInterval(that.accountInterval);
                    } else {
                      that.queryLoginStatus();
                    }
                  }, 8000)
                } else {
                  that.queryLoginStatus()
                }
              },5000);
            } else {
              that.canOrder = true
              that.$emit('client-Info', that.canOrder, that.clientEmail);
            }
          }
        })

        if (this.validate) {
          // event fire
          this.$eventFire.addToCart()
        }
      },
      emailFocusin() {
        this.tipsMsg = '';
        this.isErrorMsg = false;
        this.showTips = false;
        this.canLogin = false;
      },
      emailValidate(email) {
        const emailReg = /[\w!#$%&'*+/=?^_`{|}~-]+(?:\.[\w!#$%&'*+/=?^_`{|}~-]+)*@(?:[\w](?:[\w-]*[\w])?\.)+[\w](?:[\w-]*[\w])?/;
        return emailReg.test(email);
      },
      queryLoginStatus() {
        // try to get the email first
        let email = localStorage.getItem('AU_EMAIL')
        let uuid = localStorage.getItem('AU_UUID')
        let hmac = localStorage.getItem('AU_HMAC')
        if (email && email.length > 0 && !email.endsWith('@auvpn.net') && uuid && hmac) {
          // user logined
          clearInterval(this.accountInterval);
          this.clientEmail = email
          this.showTips = true
          this.tipsMsg = this.$t('order/place:screen3.paragrahp-line-3')
          this.canLogin = false
          this.canOrder = true
          this.isLogin = true
          this.isBoundEmail = true
          this.canSwitch = false
          this.userAddress = `https://www.ausvc.link/?uuid=${uuid}&hmac=${hmac}`
          this.$emit('client-Info', this.isBoundEmail, email);
        }
      },
      preventOpenAUAdress() {

      },
      downloadAuAccountInfo() {
        if (this.isPc) {
          const element = document.createElement('a');
          element.setAttribute('href', 'data:text/plain;charset=utf-8,' + encodeURIComponent(`${this.$t('order/place:screen3.tips-1')}\n\n${this.userAddress}`));
          element.setAttribute('download', `${this.$t('order/place:screen3.tips-1')}`);
          element.style.display = 'none';
          element.click()
        } else {
          this.copyAddr()
        }
      },
      addToDeviceDesktop() {

      },
      createAccount() {
        this.creating = true
        let param = {
          "recaptchaToken": "333333"
        };
        const that = this;
        this.$request.auCreatedNewAddress(param, function (data) {
          if (data && (data.status === 200 || data.status === 201) && data.data) {
            localStorage.setItem(AU_UUID, data.data.uuid);
            localStorage.setItem(AU_HMAC, data.data.hmac);
            that.userAddress = data.data.userAddress;
            that.creating = false
          }
        });
      }
    }
  };
</script>

