<template>
  <div class="order-place-dialog">
      <a-modal v-model="showDialog" :destroyOnClose="true" :title="title" width="700px" centered>
          <div>
              <SupportArticle v-bind:article="articleBody"></SupportArticle>
          </div>
          <template slot="footer">
              <a-button key="submit" type="primary" @click="closeDialog" style="background-color: #3963EF;">{{$t('OrderPlaceDialog.Already-Know')}}</a-button>
          </template>
      </a-modal>
  </div>
</template>
<script>
    import SupportArticle from '@/components/SupportArticle.vue';
    export default {
        name: "orderPlaceDialog",
        components: {
            SupportArticle
        },
        data(){
          return {
              isPc: this.$isPc(),
              showDialog: false,
              articleBody: '',
              title: '',
          }
        },
        props: {
            articleId: {}
        },
        watch: {
            articleId: {
                immediate: true,
                handler(val) {
                    if (val) {
                        if (val) {
                            this.showDialog = true;
                            const that = this;
                            this.$request.getZendeskArticle(this.articleId, function (data) {
                                if (data.status === 200) {
                                    that.articleBody = data.data['article']['body'];
                                    that.title = data.data['article']['title'];
                                }
                            });
                        }
                    }
                }
            }
        },
        methods:{
            closeDialog() {
                //this.articleBody = '';
                this.showDialog = false;
            },
        }
    }
</script>

